import React, { useEffect } from "react";
import PropTypes from "prop-types";
import Accordion from "@mui/material/Accordion";
import AccordionDetails from "@mui/material/AccordionDetails";
import AccordionSummary from "@mui/material/AccordionSummary";
import { useSelector, useDispatch } from "react-redux";
import { getQuestion } from "../../redux/reducers/questionsReducer";
import { makeStyles, Typography, Tabs, Tab, Box } from "@material-ui/core";
import { Container } from "@mui/material";
function TabPanel(props) {
  const { children, value, index, ...other } = props;
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}
TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};
function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

export default function FAQ({ t }) {
  const [value, setValue] = React.useState(0);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  const useStyles = makeStyles((theme) => ({
    faq: {
      padding: "80px 0 80px",
      background: "#f7f8f9",
      [theme.breakpoints.down("sm")]: {
        padding: "25px 0 25px",
      },
    },
    title: {
      fontWeight: "600",
      color: " #000000",
      letterSpacing: " -0.04em",
      fontSize: "44px",
      [theme.breakpoints.down("sm")]: {
        textAlign: "center",
        fontSize: "33px",
      },
    },
    tabs: {
      background: "#FFFFFF",
      boxShadow: " 0px 1px 0px #E2E8F0",
      borderRadius: "13px",
      padding: "24px",
      height: "min-content",
      minWidth: "288px",
      [theme.breakpoints.down("xs")]: {
        width: "100%",
      },
    },

    tab: {
      color: "#000",
      backgroundColor: "#fce34d",
      padding: "12px 0",
      borderRadius: 8,
      fontWeight: 600,
      width: "100%",
      borderBottom: "none",
      marginBottom: 16,

      "&:last-child": {
        marginBottom: 0,
      },
    },
    items: {
      marginTop: 32,
      display: "flex",
      justifyContent: "space-between",
      [theme.breakpoints.down("sm")]: {
        flexDirection: "column",
      },
    },
    tapPanel: {
      marginLeft: 40,
      "& .MuiBox-root": {
        padding: "0 !important",
      },
      [theme.breakpoints.down("sm")]: {
        marginLeft: 0,
        marginTop: 20,
      },
    },
    AccordionTitle: {
      fontSize: 28,
      [theme.breakpoints.down("sm")]: {
        fontSize: 23,
      },
    },
  }));
  const classes = useStyles();

  const dispatch = useDispatch();
  const { question } = useSelector(({ questionReducer }) => questionReducer);
  useEffect(() => {
    dispatch(getQuestion());
    // eslint-disable-next-line
  }, []);
  const arrayFaqContent = Object.values(
    typeof question.faq === "object" && question.faq
  ).map((i) => i);

  const [expanded, setExpanded] = React.useState(false);
  const handleChangeAcordion = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };
  return (
    <section className={classes.faq}>
      <Container>
        <Typography
          variant="h2"
          className={classes.title}
          style={{ lineHeight: 1.2 }}
        >
          {t("FAQTitle")}
        </Typography>

        <Box className={classes.items} style={{ padding: 0 }}>
          <Box className={classes.tabs}>
            <Tabs
              value={value}
              onChange={handleChange}
              className={classes.tabsItems}
            >
              {arrayFaqContent &&
                arrayFaqContent.map((item, index) => {
                  return (
                    <Tab
                      className={classes.tab}
                      label={item.map((item) => item.name)[0]}
                      {...a11yProps(index)}
                      key={index}
                    />
                  );
                })}
            </Tabs>
          </Box>

          <Box className={classes.tapPanel} style={{ padding: 0 }}>
            {arrayFaqContent.map((item, index) => {
              return (
                <TabPanel
                  value={value}
                  key={index}
                  index={index}
                  style={{ padding: 0 }}
                >
                  {item.map((i, index) => {
                    return (
                      <Accordion
                        key={index}
                        expanded={expanded === i}
                        onChange={handleChangeAcordion(i)}
                      >
                        <AccordionSummary
                          expandIcon={
                            <div
                              style={{
                                width: 0,
                                height: 0,
                                borderLeft: "8px solid transparent",
                                borderRight: "8px solid transparent",
                                borderTop: "10px solid #FCE34D",
                                borderRadius: 3,
                              }}
                            ></div>
                          }
                          aria-controls="panel1a-content"
                          id="panel1a-header"
                        >
                          <Typography
                            variant="body1"
                            style={{ fontWeight: 500, fontSize: 22 }}
                          >
                            {i.question}
                          </Typography>
                        </AccordionSummary>
                        <AccordionDetails>
                          <Typography
                            variant="body1"
                            style={{
                              color: "#64748B",
                              fontSize: 16,
                              lineHeight: "150%",
                            }}
                            dangerouslySetInnerHTML={{
                              __html: i.answer,
                            }}
                          />
                        </AccordionDetails>
                      </Accordion>
                    );
                  })}
                </TabPanel>
              );
            })}
          </Box>
        </Box>
      </Container>
    </section>
  );
}
